@import "../assets/styles/app";

.app {
  padding: 32px 16px;

  @include desktop {
    padding: 40px 0;
  }

  .info {
    position: fixed;
    left: 16px;
    top: 16px;
    padding: 8px 16px;
    font-size: 24px;
    border: 2px solid #000;
    border-radius: 999px;
    background: #EEE;
    transition: 0.3s;

    &_scrolling {
      background: lightgreen;
    }
  }

  .inner {
    font-size: 36px;

    @include desktop {
      width: 1040px;
      margin-left: auto;
      margin-right: auto;
    }

  }
}
